.modal {
    position: fixed;
    z-index: 500;
    width: 100%;
    top: 0;
    left: 0;
    color: #fff;
    height: 52rem;
    opacity: 0;
    box-shadow: 0 1.5rem 4rem rgba(23, 23, 24, .15);
}

.show {
    transition: .5s .3s ease-out;
    top: 20%;
    left: 0;
    visibility: visible;
    opacity: 1;
}

.hide {
    visibility: hidden;
}

.backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(17, 17, 17, .7);
    transition: all .5s;
}

.btn-x {
    position: fixed;
    background-color: #fff;
    background-position: center;
    height: 40px;
    width: 40px;
    top: 20px;
    right: 30px;
    z-index: 5000;
    border: none;
    color: black;
    font-size: 2rem;
    cursor: pointer;
}

.btn-x:hover {
    background: #7a7a7a41;
    transform: translateY(-5px);
    color: #fff;
}

.btn-x:active {
    transform: translateY(-3px);
}

.modal-details {
    padding-top: 3rem;
    height: 100%;
    padding-left: 3rem;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.966) 65%, transparent);
    width: 55%;
}

.modal-header {
    font-size: xx-large;
    font-weight: bold;
    padding-bottom: 30px;
}

.modal-overview {
    font-size: 20px;
    font-weight: bold;
    display: flex;
    margin-bottom: 30px;
}

.modal-body {
    font-size: 17px;
    color: rgba(255, 255, 255, 0.5);
    margin-bottom: 30px;
    width: 73%;
    line-height: 22px;
}

.modal-btn button {
    cursor: pointer;
    padding: 8px 20px;
    margin-right: 20px;
    color: white;
    text-transform: uppercase;
    border-radius: 2px;
    transition: all .2s;
}

.modal-btn .btn1 {
    background-color: #f80616de;
    border: none;
}

.modal-btn .btn2 {
    background-color: rgba(0, 0, 0, 0);
    border: 0.5px solid #7a7a7a;
}

.modal-btn button:hover {
    transform: scale(1.1);
}

@media only screen and (max-width: 540px) {
    .modal-header {
        font-size: x-large;
    }
    .modal-overview {
        font-size: 15px;
    }
    .modal-body {
        font-size: 17px;
        width: 100%
    }
    .modal-details {
        padding-left: 13px;
        width: 80%
    }
    .modal-btn button {
        padding: 10px 20px;
        font-size: small
    }
}

.youtube {
    position: absolute;
    top: 0;
    right: 0px;
    height: 100%;
    width: 60%;
    padding-left: 2rem;
}

@media screen and (max-width: 450px) {
    .show {
        top: 12%;
    }
    .modal-header {
        padding-top: 1rem;
        padding-bottom: 23px;
    }
    #youtube-iframer {
        margin-top: .5rem;
        height: 15rem !important;
    }
    .btn-x {
        height: 26px;
        width: 26px;
        font-size: 1rem;
    }
}